<script setup>
import StartPage from './components/StartPage.vue'
import Card from './components/Card.vue'
import img1 from './assets/sotrudnik_1.png'
import img2 from './assets/sotrudnik_2.png'
import img3 from './assets/sotrudnik_3.png'
import { ref } from 'vue'

const isStartGame = ref(false)

const workerArr = ref([
	{
		img: img1,
		name: 'Сотрудник 1',
		textArr: [
			'Доплата за совмещение должностей',
			'Стимулирующая выплата',
			'За первые два часа работы не менее чем в полуторном размере, за последующие часы - не менее чем в двойном размере',
			'Компенсация личных затрат сотрудника',
		],
		correctArr: [
			'Доплата за совмещение должностей',
			'За первые два часа работы не менее чем в полуторном размере, за последующие часы - не менее чем в двойном размере',
		],
	},
	{
		img: img2,
		name: 'Сотрудник 2',
		textArr: [
			'Компенсация личных затрат сотрудника',
			'Доплата за совмещение должностей',
			'Стимулирующая выплата',
			'За первые два часа работы не менее чем в полуторном размере, за последующие часы - не менее чем в двойном размере',
		],
		correctArr: [
			'Доплата за совмещение должностей',
			'Стимулирующая выплата',
			'За первые два часа работы не менее чем в полуторном размере, за последующие часы - не менее чем в двойном размере',
		],
	},
	{
		img: img3,
		name: 'Сотрудник 3',
		textArr: [
			'За первые два часа работы не менее чем в полуторном размере, за последующие часы - не менее чем в двойном размере',
			'Стимулирующая выплата',
			'Компенсация личных затрат сотрудника',
			'Доплата за совмещение должностей',
		],
		correctArr: [
			'За первые два часа работы не менее чем в полуторном размере, за последующие часы - не менее чем в двойном размере',
			'Стимулирующая выплата',
			'Доплата за совмещение должностей',
		],
	},
])

const currentIndex = ref(0)
const cardKey = ref(0) // Ключ компонента Card
const isLastWorker = ref(false)

// Функция для перехода к следующему сотруднику
const nextWorker = () => {
	if (currentIndex.value < workerArr.value.length - 1) {
		currentIndex.value += 1
		cardKey.value += 1 // Изменяем ключ компонента для его полной перерисовки
	} else {
		isLastWorker.value = true
	}
}

// Функция для перезапуска игры
const restart = () => {
	currentIndex.value = 0
	cardKey.value += 1 // Сбрасываем компонент в начальное состояние
}
</script>

<template>
	<Transition name="bounce" mode="out-in">
		<div v-if="!isStartGame">
			<StartPage @start="isStartGame = true"></StartPage>
		</div>

		<div v-else class="container">
			<Card
				:key="cardKey"
				:worker="workerArr[currentIndex]"
				@next="nextWorker"
			/>
		</div>
	</Transition>

	<footer></footer>
</template>

<style scoped>
footer {
	margin-top: 1.5vw;
	height: 3vw;
}
</style>
