<script setup>
import { ref, computed } from 'vue'

// Принимаем worker из пропсов
const props = defineProps(['worker'])
const emit = defineEmits(['next'])

// Массив для хранения выбранных текстов
const userSelect = ref([])

const isShowCheckBtn = ref(false)
const isChecked = ref(false)
const isShowNextBtn = ref(false)
const isShowAlert = ref(false)
const isShowEndMsg = ref(false)

// Функция для обработки клика по текстовому элементу
const toggleSelection = text => {
	if (isChecked.value) return // Отключаем взаимодействие после проверки
	isShowCheckBtn.value = true
	// Если текст уже выбран, удалить его из массива
	if (userSelect.value.includes(text)) {
		userSelect.value = userSelect.value.filter(item => item !== text)
	} else {
		// Если текст не выбран, добавить его в массив
		userSelect.value.push(text)
	}
}

const checkUserSelect = () => {
	if (props.worker.correctArr.length !== userSelect.value.length) {
		isShowAlert.value = true
		isChecked.value = true
	} else {
		// Преобразуем оба массива в Set
		const set1 = new Set(props.worker.correctArr)
		const set2 = new Set(userSelect.value)

		// Проверяем, содержит ли set1 все элементы из set2
		const allCorrect = [...set1].every(item => set2.has(item))

		if (allCorrect) {
			if (isLastWorker.value !== true) isShowNextBtn.value = true
			else isShowEndMsg.value = true

			// Окрашиваем элементы:
			// - Правильные в зелёный
			// - Неверные в красный
			// Убираем рамку для всех покрашенных элементов
			props.worker.textArr.forEach(text => {
				const element = document.querySelector(
					`.container__item-text[data-text="${text}"]`
				)
				if (element) {
					if (props.worker.correctArr.includes(text)) {
						// Если элемент правильный
						element.style.backgroundColor = '#00f700'
						element.style.border = 'none' // Убираем рамку
						element.style.color = 'black' // Убираем рамку
					} else {
						// Если элемент неправильный
						element.style.backgroundColor = 'red'
						element.style.border = 'none' // Убираем рамку
					}
				}
			})
		} else {
			isShowAlert.value = true
		}
		isChecked.value = true
	}
}

const isLastWorker = computed(() => {
	if (props.worker.name === 'Сотрудник 3') {
		return true
	} else {
		return false
	}
})

const restart = () => {
	location.reload()
}

const nextWorker = () => {
	// Сбрасываем состояние
	userSelect.value = []
	isShowCheckBtn.value = false
	isChecked.value = false
	isShowNextBtn.value = false

	// Сбрасываем стили элементов
	props.worker.textArr.forEach(text => {
		const element = document.querySelector(
			`.container__item-text[data-text="${text}"]`
		)
		if (element) {
			element.style.backgroundColor = '#fff2cc' // Возвращаем исходный цвет
			element.style.border = '1px solid black' // Возвращаем исходную рамку
			element.style.color = 'black' // Возвращаем исходный цвет текста
		}
	})

	// Переход к следующему сотруднику
	emit('next')
}
</script>

<template>
	<div v-if="isShowAlert" class="container__alert">
		<div class="alert">
			<h2>
				ВЫ ОШИБЛИСЬ! <br />
				ПРОЧИТАЙТЕ ЗАДАНИЕ ЕЩЕ РАЗ И ПОПРОБУЙТЕ СНОВА
			</h2>
			<button @click="restart" style="font-size: 0.8em">
				ПОПРОБОВАТЬ СНОВА
			</button>
		</div>
	</div>
	<div v-if="isShowEndMsg" class="container__alert">
		<div class="good">
			<h2>
				ВЫ УСПЕШНО ПРОШЛИ ТРЕНИРОВКУ. <br />
				ЧТОБЫ ПОУПРАЖНЯТСЯ ЕЩЕ РАЗ, МОЖЕТЕ ПРОЙТИ ТРЕНИРОВКУ СНОВА
			</h2>
			<button @click="restart" style="font-size: 0.8em">
				ПОПРОБОВАТЬ СНОВА
			</button>
		</div>
	</div>
	<div class="container">
		<div class="container__item">
			<h2>{{ worker.name }}</h2>
			<div class="container__img">
				<img :src="worker.img" alt="" />
			</div>
			<div
				class="container__item-text"
				v-for="(text, index) in worker.textArr"
				:key="index"
				:class="{ selected: userSelect.includes(text) }"
				@click="toggleSelection(text)"
				:data-text="text"
			>
				{{ text }}
			</div>
		</div>
		<div v-if="isShowCheckBtn" class="container__btn">
			<button v-if="!isChecked" @click="checkUserSelect">Проверить</button>
			<button
				@click="nextWorker"
				style="font-size: 0.8em"
				v-else-if="isShowNextBtn"
			>
				ПЕРЕЙТИ К СЛЕДУЮЩЕМУ СОТРУДНИКУ
			</button>
		</div>
	</div>
</template>

<style scoped>
.container__alert {
	position: absolute;
	background-color: rgba(146, 145, 145, 0.46);
	width: 100%;
	height: 100%;
}

.alert {
	position: absolute;
	top: 45vw;
	left: 50%;
	transform: translateX(-50%);
	text-align: center;
	color: red;
	background-color: #fcfbfb;
	padding: 2vw;
	max-width: 70%;
	border-radius: 4vw;
}

.good {
	position: absolute;
	top: 32vw;
	left: 50%;
	transform: translateX(-50%);
	text-align: center;
	color: #00f700;
	background-color: #fcfbfb;
	padding: 2vw;
	max-width: 70%;
	border-radius: 4vw;
}

.container {
	display: flex;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}

.container__item {
	margin-top: 2vw;
	grid-column: 2/2;
	background-color: #f3f4fb;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	padding: 2vw;
	border-radius: 4vw;
}

.container__item > * {
	margin-bottom: 2vw;
}

img {
	width: 20vw;
	border-radius: 50%;
}

.container__item-text {
	text-align: center;
	border: 1px solid black;
	padding: 1vw;
	border-radius: 1vw;
	background-color: #fff2cc;
	cursor: pointer;
	transition: background-color ease-in-out 0.3s;
}

.container__item-text:hover {
	background-color: #6292e9;
}

.container__item-text.selected {
	background-color: #0794ff; /* Синий цвет для выбранных элементов */
	color: #ffffff; /* Цвет текста при выборе */
}

.container__btn {
	grid-column: 2/2;
	margin-top: 3vw;
	display: flex;
	justify-content: center;
	animation: show 0.6s ease-in-out;
}

.container__btn button {
	padding: 1vw 3vw;
	background-color: #0794ff;
	font-size: 1em;
	color: #fafafa;
	cursor: pointer;
	transition: scale ease-in-out 0.2s, border-radius ease-in-out 0.2s;
}

.container__btn button:hover {
	scale: 1.05;
	border-radius: 1.5vw;
}

@keyframes show {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.container__alert button {
	margin-top: 2vw;
	padding: 1vw 3vw;
	background-color: #0794ff;
	font-size: 1em;
	color: #fafafa;
	cursor: pointer;
	transition: scale ease-in-out 0.2s, border-radius ease-in-out 0.2s;
}

.container__alert button:hover {
	scale: 1.05;
	border-radius: 1.5vw;
}
</style>
